// src/components/DSA/ChapterList.js
"use client";

import { useState, useEffect } from "react";
import {
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Typography,
  Box,
  CircularProgress,
  Divider,
  Chip,
  useTheme,
  alpha,
} from "@mui/material";
import {
  CheckCircle as CheckCircleIcon,
  RadioButtonUnchecked as RadioButtonUncheckedIcon,
  Code as CodeIcon,
  CheckCircle,
} from "@mui/icons-material";
import { getLevelColor } from "@/utils";

export default function ChapterList({
  topics = [],
  selectedTopic,
  selectedSubtopicId,
  onSelectTopic,
  onSelectSubtopic,
  completedSubtopicIds = [],
}) {
  const theme = useTheme();
  const [loading, setLoading] = useState(true);
  const [expandedTopic, setExpandedTopic] = useState(null);

  const handleTopicClick = (topic) => {
    if (expandedTopic === topic._id) {
      setExpandedTopic(null);
    } else {
      setExpandedTopic(topic._id);
      if (onSelectTopic) {
        onSelectTopic(topic);
      }
    }
  };

  const getCompletedCount = (topic) => {
    if (!topic.subtopics) return 0;
    return topic.subtopics.filter(
      (subtopic) =>
        completedSubtopicIds.includes(subtopic._id) ||
        subtopic.status === "completed"
    ).length;
  };
  if (loading && topics.length === 0) {
    return (
      <Box sx={{ display: "flex", justifyContent: "center", p: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  if (topics.length === 0) {
    return (
      <Box sx={{ p: 3, textAlign: "center" }}>
        <Typography color="text.secondary">No chapters available</Typography>
      </Box>
    );
  }

  return (
    <List
      component="nav"
      sx={{
        width: "100%",
        bgcolor: "background.paper",
        "& .MuiListItemButton-root": {
          borderRadius: 1,
          mb: 0.5,
        },
      }}
    >
      {topics.map((topic) => (
        <Box key={topic._id || topic.id}>
          <ListItemButton
            selected={selectedTopic?._id === topic._id}
            onClick={() => {
              // Toggle topic selection
              if (selectedTopic?._id === topic._id) {
                onSelectTopic(null);
              } else {
                onSelectTopic(topic);
              }
            }}
            sx={{
              mb: 1,
              bgcolor:
                selectedTopic?._id === topic._id ? "#3cb815" : "primary.main",
              color: "#fff",
              borderLeft:
                selectedTopic?._id === topic._id
                  ? "4px solid #002884"
                  : "4px solid transparent",
              "&:hover": {
                color: "#000",
                bgcolor:
                  selectedTopic?._id === topic._id
                    ? "#3cb815"
                    : "rgba(0, 0, 0, 0.04)",
              },
              "&.Mui-selected": {
                bgcolor: "primary.main",
                "&:hover": {
                  bgcolor: "primary.dark",
                },
              },
              transition: "all 0.2s ease",
            }}
          >
            <ListItemText
              primary={
                <Typography
                  variant="subtitle2"
                  fontWeight={selectedTopic?._id === topic._id ? 600 : 400}
                  sx={{
                    color:
                      selectedTopic?._id === topic._id ? "#fff" : "inherit",
                    pl: 1,
                  }}
                >
                  {topic.name}
                </Typography>
              }
            />
          </ListItemButton>

          {selectedTopic?._id === topic._id && topic.subtopics?.length > 0 && (
            <List component="div" disablePadding>
              {topic.subtopics.map((subtopic) => {
                const isCompleted =
                  completedSubtopicIds.includes(subtopic._id) ||
                  subtopic.status === "completed";
                return (
                  <ListItem
                    key={subtopic._id}
                    button
                    selected={selectedSubtopicId === subtopic._id}
                    onClick={() => onSelectSubtopic(subtopic)}
                    sx={{
                      pl: 4,
                      borderRadius: 1,
                      mb: 0.5,
                      "&.Mui-selected": {
                        backgroundColor: (theme) =>
                          alpha(theme.palette.primary.main, 0.1),
                        color: "primary.main",
                        borderLeft: "3px solid #3cb815",
                        "&:hover": {
                          backgroundColor: (theme) =>
                            alpha(theme.palette.primary.main, 0.15),
                        },
                        "& .MuiTypography-root": {
                          color: "primary.main",
                          fontWeight: 500,
                        },
                      },
                      "&:hover": {
                        backgroundColor: (theme) =>
                          alpha(theme.palette.primary.main, 0.05),
                      },
                      transition: "all 0.2s ease",
                    }}
                  >
                    <ListItemText
                      primary={
                        <Box
                          sx={{
                            display: "flex",
                            alignItems: "center",
                            gap: 1,
                            width: "100%",
                          }}
                        >
                          <Typography
                            variant="body2"
                            color={
                              selectedSubtopicId === subtopic._id
                                ? "primary.main"
                                : "text.primary"
                            }
                            fontWeight={
                              selectedSubtopicId === subtopic._id ? 600 : 400
                            }
                            sx={{
                              flexGrow: 1,
                              overflow: "hidden",
                              textOverflow: "ellipsis",
                              whiteSpace: "nowrap",
                            }}
                          >
                            {subtopic.name}
                          </Typography>
                          {isCompleted && (
                            <CheckCircle
                              fontSize="small"
                              color={
                                selectedSubtopicId === subtopic._id
                                  ? "primary"
                                  : "success"
                              }
                              sx={{
                                ml: "auto",
                                flexShrink: 0,
                              }}
                            />
                          )}
                        </Box>
                      }
                    />
                  </ListItem>
                );
              })}
            </List>
          )}
        </Box>
      ))}
    </List>
  );
}
