// In ChapterDetail.js
"use client";

import { useState, useEffect } from "react";
import {
  Box,
  Typography,
  Paper,
  Button,
  Chip,
  Stack,
  CircularProgress,
  Snackbar,
  Alert,
} from "@mui/material";
import { CheckCircle as CheckCircleIcon, YouTube, Code, Article } from "@mui/icons-material";
import api from "@/apiHandler/page";
import { getLevelColor } from "@/utils";

const ChapterDetail = ({ chapter, subtopics, selectedSubtopicId, onUpdate }) => {
  const [currentSubtopic, setCurrentSubtopic] = useState(null);
  const [loading, setLoading] = useState(false);
  const [snackbar, setSnackbar] = useState({ 
    open: false, 
    message: "", 
    severity: "success" 
  });

  // Update current subtopic when selectedSubtopicId or subtopics change
  useEffect(() => {
    if (selectedSubtopicId && subtopics?.length > 0) {
      const subtopic = subtopics.find(st => st._id === selectedSubtopicId);
      if (subtopic) {
        setCurrentSubtopic(subtopic);
        return;
      }
    }
    if (subtopics?.length > 0) {
      setCurrentSubtopic(subtopics[0]);
    } else {
      setCurrentSubtopic(null);
    }
  }, [selectedSubtopicId, subtopics]);

  const handleMarkAsCompleted = async (subtopicId, isCurrentlyCompleted) => {
    try {
      setLoading(true);
      const endpoint = isCurrentlyCompleted ? "uncomplete" : "complete";
      await api.post(`/subtopics/${subtopicId}/${endpoint}`);
      
      setSnackbar({
        open: true,
        message: isCurrentlyCompleted 
          ? "Marked as not completed" 
          : "Marked as completed!",
        severity: "success",
      });
      
      // Update the parent component
      if (onUpdate) {
        await onUpdate();
      }
      
      // Update local state
      setCurrentSubtopic(prev => ({
        ...prev,
        isCompleted: !isCurrentlyCompleted
      }));

    } catch (error) {
      console.error("Error updating completion status:", error);
      setSnackbar({
        open: true,
        message: "Failed to update status. Please try again.",
        severity: "error",
      });
    } finally {
      setLoading(false);
    }
  };

  const handleCloseSnackbar = () => {
    setSnackbar(prev => ({ ...prev, open: false }));
  };

  if (!currentSubtopic) {
    return (
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        minHeight={300}
      >
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Box>
      <Paper
        elevation={0}
        sx={{
          borderRadius: 2,
          overflow: "hidden",
          mb: 3,
          border: "1px solid",
          borderColor: "divider",
        }}
      >
        <Box
          sx={{
            p: { xs: 2, sm: 3 },
            borderBottom: "1px solid",
            borderColor: "divider",
          }}
        >
          <Typography variant="h5" component="h1" gutterBottom>
            {currentSubtopic.name}
          </Typography>
          <Stack direction="row" spacing={1} sx={{ mb: 2 }}>
            <Chip
              label={currentSubtopic.difficulty || "Easy"}
              size="small"
              color={getLevelColor(currentSubtopic.difficulty)}
              sx={{ textTransform: "capitalize" }}
            />
            {currentSubtopic.isCompleted && (
              <Chip
                icon={<CheckCircleIcon fontSize="small" />}
                label="Completed"
                color="success"
                size="small"
                variant="outlined"
              />
            )}
          </Stack>
        </Box>

        <Box sx={{ p: { xs: 2, sm: 3 } }}>
          {currentSubtopic.description && (
            <Box sx={{ mb: 4 }}>
              <Typography variant="body1" paragraph>
                {currentSubtopic.description}
              </Typography>
            </Box>
          )}

          <Box sx={{ display: "flex", flexWrap: "wrap", gap: 2, mb: 3 }}>
            <Button
              variant={currentSubtopic.isCompleted ? "outlined" : "contained"}
              color="primary"
              onClick={() => 
                handleMarkAsCompleted(
                  currentSubtopic._id, 
                  currentSubtopic.isCompleted
                )
              }
              disabled={loading}
              startIcon={
                loading ? (
                  <CircularProgress size={20} color="inherit" />
                ) : (
                  <CheckCircleIcon />
                )
              }
              sx={{ 
                mr: 2, 
                mb: { xs: 2, sm: 0 },
                minWidth: 200
              }}
            >
              {currentSubtopic.isCompleted 
                ? "Mark as Not Completed" 
                : "Mark as Completed"}
            </Button>

            {currentSubtopic.youtubeLink && (
              <Button
                variant="outlined"
                href={currentSubtopic.youtubeLink}
                target="_blank"
                rel="noopener noreferrer"
                startIcon={<YouTube />}
                sx={{ mr: 2, mb: { xs: 2, sm: 0 } }}
              >
                Watch Video
              </Button>
            )}

            {currentSubtopic.leetcodeLink && (
              <Button
                variant="outlined"
                href={currentSubtopic.leetcodeLink}
                target="_blank"
                rel="noopener noreferrer"
                startIcon={<Code />}
                sx={{ mr: 2, mb: { xs: 2, sm: 0 } }}
              >
                LeetCode
              </Button>
            )}

            {currentSubtopic.articleLink && (
              <Button
                variant="outlined"
                href={currentSubtopic.articleLink}
                target="_blank"
                rel="noopener noreferrer"
                startIcon={<Article />}
              >
                Read Article
              </Button>
            )}
          </Box>
        </Box>
      </Paper>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={3000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert 
          onClose={handleCloseSnackbar} 
          severity={snackbar.severity}
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default ChapterDetail;