"use client";

import { useState, useEffect } from "react";
import {
  Box,
  CircularProgress,
  Container,
  Grid,
  Typography,
  useTheme,
} from "@mui/material";
import ChapterList from "@/components/DSA/ChapterList";
import ChapterDetail from "@/components/DSA/ChapterDetail";
import ProgressTracker from "@/components/DSA/ProgressTracker";
import api from "@/apiHandler/page";

export default function DSAPage() {
  const theme = useTheme();
  const { palette } = theme;
  const [topics, setTopics] = useState([]);
  const [selectedTopic, setSelectedTopic] = useState(null);
  const [selectedSubtopic, setSelectedSubtopic] = useState(null);

  const [completedSubtopicIds, setCompletedSubtopicIds] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const fetchData = async () => {
    try {
      setLoading(true);
      console.log("Fetching topics and completed subtopics...");

      const [topicsRes, completedRes] = await Promise.all([
        api.get("/topics/all"),
        api.get("/subtopics/completed"),
      ]);

      console.log("Topics response:", topicsRes);
      console.log("Completed subtopics response:", completedRes);

      const topicsData = topicsRes.data || [];
      setTopics(topicsData);

      // Handle both array and object responses for completed subtopics
      const completedData = Array.isArray(completedRes.data)
        ? completedRes.data
        : completedRes.data?.completed || completedRes.data?.items || [];

      const completedIds = completedData
        .map((item) => (typeof item === "string" ? item : item._id || item.id))
        .filter(Boolean);

      console.log("Completed subtopic IDs:", completedIds);
      setCompletedSubtopicIds(completedIds);

      // Update subtopics with completed status
      const updatedTopics = topicsData.map((topic) => ({
        ...topic,
        subtopics:
          topic.subtopics?.map((subtopic) => ({
            ...subtopic,
            isCompleted: completedIds.includes(subtopic._id),
          })) || [],
      }));

      setTopics(updatedTopics);

      // Select first topic by default if none selected
      if (topicsData.length > 0 && !selectedTopic) {
        const firstTopic = updatedTopics[0];
        setSelectedTopic(firstTopic);
        // Select first subtopic by default
        if (firstTopic.subtopics?.length > 0) {
          setSelectedSubtopic(firstTopic.subtopics[0]);
        }
      }
    } catch (err) {
      console.error("Error details:", {
        message: err.message,
        response: err.response?.data,
        status: err.response?.status,
      });
      setError("Failed to load data. Please try again later.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const handleTopicSelect = (topic) => {
    // Toggle topic selection - if the same topic is clicked, deselect it
    if (selectedTopic?._id === topic._id) {
      setSelectedTopic(null);
      setSelectedSubtopic(null);
    } else {
      setSelectedTopic(topic);
      // Auto-select the first subtopic when selecting a new topic
      if (topic.subtopics?.length > 0) {
        setSelectedSubtopic(topic.subtopics[0]);
      } else {
        setSelectedSubtopic(null);
      }
    }
  };

  const handleSubtopicSelect = async (subtopic) => {
    try {
      // If clicking the same subtopic, deselect it
      if (selectedSubtopic?._id === subtopic._id) {
        setSelectedSubtopic(null);
        return;
      }

      setLoading(true);
      // Fetch detailed subtopic data
      const response = await api.get(`/subtopics/${subtopic._id}`);
      setSelectedSubtopic(response.data);

      // Update the selected topic to the parent topic of this subtopic
      if (selectedTopic?._id !== subtopic.topic) {
        const parentTopic = topics.find((t) => t._id === subtopic.topic);
        if (parentTopic) {
          setSelectedTopic(parentTopic);
        }
      }
    } catch (error) {
      console.error("Error fetching subtopic details:", error);
    } finally {
      setLoading(false);
    }
  };

  const handleUpdate = async () => {
    try {
      const response = await api.get("/subtopics/completed");
      const completedSubtopics = response.data || [];
      setCompletedSubtopicIds(
        completedSubtopics
          .map((item) => item.subtopic?._id || item.subtopic)
          .filter(Boolean)
      );

      // Update the topics with the latest completion status
      setTopics((prevTopics) =>
        prevTopics.map((topic) => ({
          ...topic,
          subtopics:
            topic.subtopics?.map((subtopic) => {
              const completed = completedSubtopics.some(
                (item) => (item.subtopic?._id || item.subtopic) === subtopic._id
              );
              return {
                ...subtopic,
                isCompleted: completed,
                status: completed ? "completed" : "pending",
              };
            }) || [],
        }))
      );
    } catch (err) {
      console.error("Error refreshing completed subtopics:", err);
    }
  };

  if (loading && topics.length === 0) {
    return (
      <Container maxWidth="xl" sx={{ py: 4 }}>
        <Box display="flex" justifyContent="center" p={4}>
          <CircularProgress />
        </Box>
      </Container>
    );
  }

  if (error) {
    return (
      <Container maxWidth="xl" sx={{ py: 4 }}>
        <Box p={4} textAlign="center">
          <Typography color="error">{error}</Typography>
        </Box>
      </Container>
    );
  }

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      <Typography
        variant="h4"
        component="h1"
        gutterBottom
        sx={{
          color: "primary.main",
          fontWeight: 600,
          mb: 3,
        }}
      >
        DSA Learning Path
      </Typography>
      <Typography
        variant="subtitle1"
        color="text.secondary"
        paragraph
        sx={{
          mb: 4,
          fontSize: "1.05rem",
          fontWeight: 400,
        }}
      >
        Master Data Structures and Algorithms with our comprehensive guide
      </Typography>

      <Grid
        container
        spacing={3}
        sx={{
          mt: 2,
          "& .MuiPaper-root": {
            bgcolor: "background.paper",
            boxShadow: "0 1px 3px rgba(0,0,0,0.05)",
            border: "1px solid",
            borderColor: "divider",
          },
        }}
      >
        <Grid size={{ xs: 12, md: 4, lg: 3 }}>
          <Box
            sx={{
              position: "sticky",
              top: 16,
              bgcolor: "background.paper",
              borderRadius: 2,
              p: 2,
              border: "1px solid",
              borderColor: "divider",
              boxShadow: "0 1px 3px rgba(0,0,0,0.05)",
              maxHeight: "calc(100vh - 120px)",
              overflowY: "auto",
              backgroundColor: "background.paper",
              borderRadius: 2,
              p: 2,
              border: "1px solid",
              borderColor: "divider",
              boxShadow: "0 1px 3px rgba(0,0,0,0.05)",
              "&::-webkit-scrollbar": {
                width: "6px",
              },
              "&::-webkit-scrollbar-track": {
                background: "bggray200",
                borderRadius: "10px",
              },
              "&::-webkit-scrollbar-thumb": {
                background: "primary.main",
                borderRadius: "10px",
                "&:hover": {
                  background: "primary.lightHover",
                },
              },
            }}
          >
            <ChapterList
              topics={topics}
              selectedTopic={selectedTopic}
              selectedSubtopicId={selectedSubtopic?._id}
              onSelectTopic={handleTopicSelect}
              onSelectSubtopic={handleSubtopicSelect}
              completedSubtopicIds={completedSubtopicIds}
            />
          </Box>
        </Grid>

        <Grid size={{ xs: 12, md: 8, lg: 6 }}>
          {selectedTopic && (
            <ChapterDetail
              chapter={selectedTopic}
              subtopics={selectedTopic.subtopics || []}
              selectedSubtopicId={selectedSubtopic?._id}
              onUpdate={handleUpdate}
            />
          )}
        </Grid>

        <Grid size={{ xs: 12, lg: 3 }}>
          {" "}
          <Box
            sx={{
              position: "sticky",
              top: 16,
            }}
          >
            <ProgressTracker
              topics={topics}
              completedSubtopicIds={completedSubtopicIds}
            />
          </Box>
        </Grid>
      </Grid>
    </Container>
  );
}
